@import url(https://fonts.googleapis.com/css2?family=Hind+Siliguri&display=swap);
body {
  margin: 0;
  font-family: "Hind Siliguri";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color:whitesmoke;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


section{min-height:calc(100vh - 8rem - 5rem);padding:2rem}footer{min-height:calc(100vh - 8rem);padding:2rem}.title{padding-top:1rem;text-align:center;line-height:4rem;font-size:2rem;color:#505050;background:rgba(100,190,189,0.219)}

.header-wrapper{height:8rem;position:relative}.header-wrapper header{color:white;position:fixed;top:0;width:100%;height:8rem;display:flex;align-items:center;flex-direction:column;justify-content:space-between;background-color:#129793}.header-wrapper header .heading{display:flex;align-items:baseline}.header-wrapper header .heading .logo{margin-top:1rem}.header-wrapper header .heading .download{-webkit-transform:rotate(90deg);transform:rotate(90deg);text-decoration:none;color:white;font-size:1.5rem}.header-wrapper header ul{display:flex;width:90%;margin:auto;justify-content:space-evenly}.header-wrapper header ul li{padding:1rem;list-style-type:none}.header-wrapper header ul li:hover{cursor:pointer;background:white;color:#129793;border-radius:1rem}

footer{background:#1d1d1d;width:100%;display:flex;flex-direction:column;text-align:center;justify-content:space-between}footer h3{text-align:center;height:2rem}footer .footer{border:1px solid white;height:calc( 100vh - 4rem - 8rem - 2rem - 4rem);width:100%;display:flex;align-items:center;text-transform:capitalize}footer .footer>div{width:calc(100% / 3);display:flex;flex-direction:column;justify-content:center;align-items:center}

.light-grey{background:#505050;color:white}.experience-wrapper{background:#505050;padding:2rem}.experience-wrapper .experience{padding:0.5rem 0rem}.experience-wrapper .company{font-size:2rem;color:aquamarine}.experience-wrapper .title-wrapper{display:flex;width:100%;justify-content:space-between}.experience-wrapper .title-wrapper .job-title{font-style:italic}.experience-wrapper .title-wrapper .time{color:aquamarine;padding-right:2rem}.experience-wrapper .responsibilities li{margin:0rem 2rem;list-style-type:circle}

.edu-wrapper{background:rgba(85,247,193,0.884);color:black;display:flex;flex-direction:column;align-items:flex-end;justify-content:center;text-align:end}.edu-wrapper .college{font-size:1.5rem}.light-red{background:rgba(85,247,193,0.884)}

.light-blue{background:#9BD7D5}.projects{background:#9BD7D5;color:black;display:flex;justify-content:center;align-items:center;text-align:center}.projects h3{text-transform:capitalize;letter-spacing:1rem;margin-bottom:1rem;font-size:large;text-align:center}.projects span{color:#129793}.projects ul{list-style-type:none}.projects ul li{color:#505050;padding:0.5rem}

.skills-page{color:black;padding:2rem;text-align:center}

